@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.form-title {
	display: block;
	@include font-alt-bold;
	@include font($font-size-h2, $line-height-h2);
	color: $title-color;
	text-align: center;
	margin-bottom: $bottom-space--md; }

.user-form {
	background: $bg-color-alt;
	@include padding-vertical($text-layout-space);
	margin-bottom: $bottom-space;
	border: solid $border-color;
	border-width: 1px 0;

	&:last-child {
		margin-bottom: 0; }

	@include media('<tab-lg') {
		@include padding-vertical($text-layout-space--sm); }

	@include media('<mob-lg') {
		@include padding-vertical($text-layout-space--mob); }

	form {
		display: block; }

	.input-wrap {
		margin-bottom: 50px; }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.captcha {
		@include display-inline-block-top;
		@include clear-spacing;
		position: relative; // Важно! Для позицирования отметки подтверждения полей формы
		z-index: 1; // Важно! Отметка подтверждения полей формы имеет z-index: -1

		@include clear;

		img {
			$margin-right: 20px;
			display: inline-block;
			vertical-align: middle;
			max-width: calc(50%);
			margin-right: $margin-right;

			& + input {
				display: inline-block;
				vertical-align: middle;
				width: 200px; } }

		@include media('<tab-lg') {
			img {
				display: block;
				max-width: none;
				margin-bottom: $bottom-space--sm;

				& + input {
					display: block;
					max-width: none;
					width: 100%; } } } }

	.form-footer {
		@include clear;

		@include media('<mob-lg') {
			padding-top: $bottom-space--sm; } }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		.btn-submit {
			width: 200px; } }

	.form-note {
		overflow: hidden;
		min-height: 50px;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--sm, $line-height--sm);
		letter-spacing: normal; } }

/* user form end */

/* slick sliders */
// slider
.slick-slide {
	&, & a {
		-webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
		-webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
		outline: none !important; } }

// arrows
.slick-arrow {
	@include size(nth($slider-btn-size, 1), nth($slider-btn-size, 2));
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition: {
		property: background, opacity, visibility;
		duration: 0.2s; }

	@include media('<mob-lg') {
		@include size(nth($slider-btn-size--sm, 1), nth($slider-btn-size--sm, 2));
		background-color: red; }

	.no-touchevents &:hover {
		opacity: 1;
		background: rgba(195, 195, 195, 0.2); }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size($slider-arr-size);
		transform: rotate(45deg);
		border: 0 solid #999;
		margin: -15px -10px;

		@include media('<mob-lg') {
			@include size($slider-arr-size--sm);
			margin: -6px -4px; } } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 3px 3px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 3px 3px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;
	visibility: hidden;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 5px 15px;
			margin: 0;

			@include media('<mob-lg') {
				@include padding-horizontal(12px); }

			&::before {
				content: none; } }

		button {
			@include size(12px);
			padding: 0;
			border-radius: 0;
			background: $base-color;
			position: relative;
			transition: {
				property: background, border, box-shadow, transform;
				duration: 0.6s; }
			visibility: visible;

			@include media('<mob-lg') {
				@include size(10px); }

			&::before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				background: #fff;
				border-radius: 0;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
				transition: {
					property: transform, opacity;
					duration: $animation-speed; } } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* swiper slider & default arrows */
.swiper-button-prev, .swiper-button-next, .def-arrows--prev, .def-arrows--next {
	overflow: hidden;
	text-indent: -9999px;
	@include size(nth($slider-btn-size, 1), nth($slider-btn-size, 2));
	top: 0;
	margin-top: 0;
	will-change: transform;
	//background: rgba(255, 127, 80, 0.51) // для тестирования, чтобы видеть контуры кнопки
	background: none;

	@include media('<mob-lg') {
		@include size(nth($slider-btn-size--sm, 1), nth($slider-btn-size--sm, 2)); }

	&::after {
		top: 50%;
		content: "";
		display: block;
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size($slider-arr-size);
		transform: rotate(45deg);
		border: 0 solid #999;
		margin: -15px -10px;

		@include media('<mob-lg') {
			@include size($slider-arr-size--sm);
			margin: -6px -4px; } } }

.swiper-button-prev, .def-arrows--prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 3px 3px; } }

.swiper-button-next, .def-arrows--next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 3px 3px 0 0; } }

// hover & disabled
.swiper-button-prev, .swiper-button-next {

	// hover
	&:not(.swiper-button-disabled):hover {
		opacity: 1;
		background: rgba(195, 195, 195, 0.2); }

	// disabled
	&.swiper-button-disabled {
		pointer-events: auto; } }
// no js
.no-js {
	.swiper-slide {
		flex-shrink: 1;
		display: inline-block;
		vertical-align: top;
		width: 320px; }

	.swiper-button-prev, .swiper-button-next, .def-arrows--prev, .def-arrows--next {
		display: none; } }

/* swiper slider end */

/* images slider */
.images-slider {
	position: relative;
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--lg; } }

.images-slider__list, .slick-dotted.images-slider__list {
	padding-bottom: $bottom-space;
	margin-bottom: 0;
	position: relative;
	z-index: 2; }

.images-slider__item {
	float: none;
	display: inline-block;
	vertical-align: top;
	opacity: 0;
	white-space: normal;
	overflow: hidden;
	position: relative;
	background-color: transparent;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1;
		margin-bottom: $bottom-space; }

	&::before {
		content: '';
		display: block;
		@include ratio(16, 9);

		.no-js & {
			content: none; } }

	img {
		position: absolute;
		@include position(0);
		text-align: center;
		max-width: none;
		width: 100%;
		height: 100%;
		@include object-fit(contain);

		.no-js &[data-lazy] {
			display: none; }

		.no-js & {
			position: relative;
			@include position(auto);
			width: auto;
			height: auto;
			@include object-fit(fill);
			margin-bottom: 1rem; } }

	.caption {
		display: none;

		.no-js & {
			display: block; } } }

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0); }

	.slick-prev {
		right: 40px; }

	// slider counter
	.slider-counter {
		position: absolute;
		left: 0;
		top: 100%;
		color: $text-color-light;

		.slide-curr {
			color: $text-color; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: $bottom-space + 1;

		li {
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition: {
					property: opacity, transform;
					duration: $animation-speed; }

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

/* images slider end */

/* flashes */
.flashes {
	padding: 0 90px; }

.flashes__item, .images-slider__item .caption {
	@include font(1.4rem, 1.5);
	color: $text-color-light;

	& > *:last-child {
		margin-bottom: 0; } }

/* flashes end */

/* promo slider */
// slider
.promo-slider__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end

	&, &.slick-dotted {
		margin: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 } }		// after slide load end

// slide
.promo-slider__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 }		// after slide load end

	img {
		display: block;
		margin: 0 auto; } }

.promo-slider__list {

	// dots
	.slick-dots {
		bottom: 20px;

		@include media('<mob-lg') {
			bottom: 10px; } } }

/* promo slider end */

/* partners slider */
// slider
.partners-slider__list {
	// before slide load
	white-space: nowrap;
	overflow: hidden;
	// before slide load end
	@include padding-horizontal(nth($slider-btn-size, 1));

	@include media('<mob-lg') {
		@include padding-horizontal(nth($slider-btn-size--sm, 1)); }

	&, &.slick-dotted {
		margin: 0; }

	&.slick-initialized, .no-js & {
		// after slide load
		white-space: normal;
		overflow: visible;
 } }		// after slide load end

// slide
.partners-slider__item {
	// before slide load
	float: none;
	display: inline-block;
	vertical-align: top;
	// before slide load end

	.slick-initialized & {
		// after slide load
		float: left;
		display: block;
 } }		// after slide load end

.partners-slider__inner {
	text-decoration: none;
	color: inherit;
	height: 150px;
	display: flex;
	align-items: center;
	@include padding-horizontal(2rem);

	@include media('<mob-lg') {
		@include padding-horizontal(1rem);
		height: 100px; }

	img {
		display: block;
		margin: 0 auto;
		filter: grayscale(1);
		transition: {
			property: filter;
			duration: $animation-speed; } }

	// hover
	&:hover {
		img {
			filter: grayscale(0); } } }

.partners-slider__list {
	// dots
	.slick-dots {
		bottom: 20px; } }

/* partners slider end */

/* btn-to-top */
.btn-to-top {
	position: fixed;
	right: $text-layout-space;
	bottom: $text-layout-space;
	@include size(50px);
	color: #000;
	border: 2px solid currentColor;
	background: rgba(255, 255, 255, 0.46);
	z-index: 9999;
	text-indent: -9999px;
	overflow: hidden;
	//=====================
	opacity: 0;
	//transform: translate(200px, 200px)
	//transform: translate3d(200px, 200px, 0)
	transform: scale(0, 0);
	transform: scale3d(0, 0, 0);
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	@include media('<tab-lg') {
		right: $text-layout-space--sm;
		bottom: $text-layout-space--sm; }

	@include media('<mob-lg') {
		right: $text-layout-space--mob;
		bottom: $text-layout-space--mob; }

	// убрать подсветку при клике на тачскринах
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	&.btn-to-top--show {
		opacity: 1;
		//transform: translate(0, 0)
		//transform: translate3d(0, 0, 0)
		transform: scale(1, 1);
		transform: scale3d(1, 1, 1); }

	&::before, &::after {
		content: '';
		position: absolute;
		top: 21px;
		display: block;
		height: 2px;
		width: 14px;
		background: currentColor; }

	&::before {
		transform: rotate(-45deg);
		left: 12px; }

	&::after {
		transform: rotate(45deg);
		left: 21px; } }

// hover
.btn-to-top {
	.no-touchevents &:hover {
		color: $base-color; } }

/* btn-to-top end */

.main-section {
	position: relative;
	margin-bottom: 1rem; }

.main-section__title {
	text-align: center;
	margin-bottom: 3rem;
	@include font(3.0rem, round-float(36/30, 2));

	@include media('<mob-lg') {
		margin-bottom: 2.4rem;
		font-size: 2.4rem; }

	h1, h2, h3 {
		@include font-alt-medium;
		letter-spacing: 0.1em;
		font-size: inherit;
		line-height: inherit;
		text-align: center;
		text-transform: uppercase;
		margin-bottom: 0; } }

// main-section--banners
.main-section--banners {
	margin-bottom: 0; }

// main-section--partners
.main-section--partners {
	margin-bottom: 2rem; }

// main-section--new
.main-section--new {
	margin-bottom: 6rem;

	@include media('<mob-lg') {
		margin-bottom: 3rem; } }

// main-section--similar
.main-section--similar {
	margin-bottom: 2rem; }

// main-section--club
.main-section--club {
	margin-bottom: 6rem;
	@include padding-horizontal($text-layout-space);

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space--sm); }

	@include media('<mob-lg') {
		@include padding-horizontal($text-layout-space--mob);
		margin-bottom: 3rem; } }

