/* breadcrumbs */
.breadcrumbs {
	color: $text-color-light;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0 6px 0 0;
			margin: 0;

			&::before, &:first-child::before {
				content: none; } } }

	cursor: default;
	overflow: hidden;
	@include font($font-size--sm, $line-height--sm);

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '•';
			border: none;
			color: $base-color;
			padding-left: 10px; }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: $text-color;
		text-decoration: none;
		cursor: pointer;

		span {
			border-bottom: 1px solid rgba(white, 0.3); }

		.no-touchevents &:hover {
			color: $base-color;

			span {
				border-color: rgba($base-color, 0.3); } } } }

/* breadcrumbs end */

/* files links */
.file {
	$file-icon-size: 42px;
	@include display-inline-block-top;
	min-height: $file-icon-size;
	padding: 0.5em;
	@include font(16px, 20px);
	color: $text-color;
	text-decoration: none;

	@include clear;

	svg {
		float: left;
		@include size($file-icon-size); }


	& > span, & > em {
		display: block;
		margin-left: #{$file-icon-size};
		padding-left: 1em; }

	& > em {
		display: block;
		font-size: 1.4rem;
		color: $text-color-light; }

	.no-touchevents &:hover {
		background-color: $base-color;

		& > em {
			color: $bg-color-base; } } }

.st-bg {
	fill: #e2e5e7; }

.st-corner {
	fill: #b0b7bd; }

.st-shadow {
	fill: #cad1d8; }

.st-text {
	fill: #fff; }

/* files links end */

/* feature list */
.feature-list {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__item {
	margin-bottom: $bottom-space;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__img {
	float: left;
	width: 25%;
	margin: 0.3em $bottom-space $bottom-space--sm 0;

	@include media("<mob-lg") {
		width: auto;
		float: none;
		margin-right: 0; } }

.feature-list__content {
	overflow: hidden;

	h3, p {
		margin-bottom: $bottom-space--sm;

		&:last-child {
			margin-bottom: 0; } } }

/* feature list end */

/*social networks */
.social-networks {
	margin-bottom: $bottom-space;
	@include clear;

	&:last-child {
		margin-bottom: 0; }

	.footer & {
		text-align: center; } }

.social-networks__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	@include font(14px, 1.5);
	letter-spacing: normal;
	margin: 0 -5px -10px; }

.social-networks__item {
	float: left;
	margin: 0 5px 10px;

	a {
		display: block;
		position: relative;
		@include size(50px);
		border-radius: 50%;
		background: #999;
		color: white;

		span {
			@include hide-text; }

		svg {
			@include centering-transform-abs;
			@include size(20px); }

		path {
			fill: currentColor; }

		.no-touchevents &:hover {
