@import "../utils/functions";
@import "../utils/placeholders";

// medium query
//$breakpoints: ('mob-sm': 320px, 'mob-md': 480px, 'mob-lg': 640px, 'tab-sm': 768px, 'tab-lg': 992px, 'desk-sm': 1200px, 'desk-md': 1366px, 'desk-lg': 1600px, 'desk-hd': 1920px)
$breakpoints: ('desk-qhd': 2560px, 'desk-hd': 1920px, 'desk-lg': 1600px, 'desk-ml': 1440px, 'desk-md': 1366px, 'desk-sm': 1200px, 'tab-lg': 992px, 'tab-sm': 768px, 'mob-lg': 640px, 'mob-md': 480px, 'mob-sm': 360px);

// base
$font-size: 1.6rem !default;
$line-height: round-float(2.4rem/$font-size, 2) !default;
// ===============
$font-size--sm: 1.4rem !default;
$line-height--sm: round-float(2.4rem/$font-size--sm, 2) !default;
// ===============
$font-size--less: 1.2rem !default;
$line-height--less: round-float(2.0rem/$font-size--less, 2) !default;

// =======h1========
$font-size-h1: 4.8rem !default;
$line-height-h1: round-float(6.0rem/$font-size-h1, 2) !default;
// md ===
$font-size-h1--md: 3.6rem !default;
$line-height-h1--md: round-float(4.8rem/$font-size-h1--md, 2) !default;
// sm ===
$font-size-h1--sm: 3.0rem !default;
$line-height-h1--sm: round-float(4.2rem/$font-size-h1--sm, 2) !default;

// =======h2========
$font-size-h2: round-float($font-size-h1*3.6rem/4.8rem, 1) !default;
$line-height-h2: round-float(4.8rem/3.6rem, 2) !default;
// md ===
$font-size-h2--md: round-float($font-size-h1--md*0.84, 1) !default;
$line-height-h2--md: round-float(($line-height-h1--md*1.02),2) !default;
// sm ===
$font-size-h2--sm: round-float($font-size-h1--sm*0.87, 1) !default;
$line-height-h2--sm: round-float(($line-height-h1--sm*1.01),2) !default;

// =======h3========
$font-size-h3: round-float($font-size-h1*2.4rem/4.8rem, 1) !default;
$line-height-h3: round-float(3.6rem/2.4rem, 2) !default;
// sm ===
$font-size-h3--sm: round-float($font-size-h1--sm*0.73, 1) !default;
$line-height-h3--sm: round-float(($line-height-h1--sm*1.05),2) !default;

// =======h4========
$font-size-h4: round-float($font-size-h1*1.8rem/4.8rem, 1) !default;
$line-height-h4: round-float(3.0rem/1.8rem, 2) !default;

// =======h5========
$font-size-h5: $font-size !default;
$line-height-h5: $line-height !default;

// =======h6========
$font-size-h6: round-float($font-size*0.75, 1) !default;
$line-height-h6: round-float($line-height*1.25, 2) !default;

$bottom-space: round-float($font-size*$line-height, 1) !default;
$bottom-space--lg: round-float($font-size*$line-height*3, 1) !default;
$bottom-space--md: round-float($font-size*$line-height*2.0, 1) !default;
$bottom-space--ml: round-float($font-size*$line-height*1.5, 1) !default;
$bottom-space--sm: round-float($font-size*$line-height*0.75, 1) !default;

// =======Цветовая схема========
// цвет бренда
$base-color: #f70106 !default;
$base-color-alt: lightskyblue !default;
// главные контрастные цвета (обычно, белый и черный)
$text-color-base: black !default;
$bg-color-base: white !default;
// цвет фона страницы
$bg-color: white !default;
// цвет фона элементов выделяющихся на страницы
$bg-color-alt: #f3f3f3 !default;
// цвет основного текста
$text-color: #000 !default;
// цвет основных заголовков
$title-color: black !default;
// цвет текста светлее основного
$text-color-light: gray !default;
// цвет ссылки
$link-color: blue !default;
// цвет линий и границ
$border-color: #666 !default;
// цвет специальных элементов
$mark-color: tomato !default;

// =======Класс текстовой страницы========
// user content class
// class на контейнере, в котором будет использоваться стилизация
// типографических элементов: списков, таблиц, цытат и т.д.
$user-content: user-content !default;

// =======Layout========
// отступы слева и справа для всего сайта
$page-layout-space: 0 0 !default;
// максимальная ширина сайта
$page-layout-width: 1920px !default;
// минимальные отступы слева и справа у текствого блока
$text-layout-space: 30px !default;
//$text-layout-space--md: 25px !default
$text-layout-space--sm: 20px !default;
$text-layout-space--mob: 10px !default;
// максимальная ширина текстового блока
$text-layout-width: 800px !default;
// максимальная ширина текстового блока выходящего за пределы текста
$flood-layout-width: 1000px !default;
// отступы внутри блочных элементов
$grid-space: 30px !default;
$grid-space--lg: 20px !default;
$grid-space--sm: 10px !default;
// отступы в дефолтных списках
$list-space: 30px;

// header
$header-height: 70px !default;
$header-height--sm: 50px !default;
$header-bg: #1a1a1a !default;

// sidebar
$sidebar-width: 260px !default;

// aside
$aside-width: 160px !default;

// logo
$logo-size: 309px 42px !default;

// shutter
$shutter-width: 280px !default;
$shutter-space: 20px 30px !default;

// nav
$nav-width: 280px !default;
$nav-space: 20px 30px !default;
$nav-arrow-size: 50px !default;
$nav-text-color: $text-color !default;
$nav-bg-color: #333 !default;
$nav-bg-color-s: #333 !default;
$nav-bg-color-ss: #333 !default;
$nav-bg-color-sss: #333 !default;
$nav-bg-color-ssss: #333 !default;

// =======Form========
$form-text-color: $text-color !default;
$form-placeholder-color: #999 !default;
$form-bg-color: white !default;
$form-border-color: #ccc !default;
$form-border-focus-color: darken($form-border-color, 20%) !default;
$form-error-color: red !default;
$form-success-color: green !default;
$form-bottom-space: 3rem !default;
$form-bottom-space--sm: 1.5em !default;
$form-gap: 40px !default;
// ========checkbox&radio==========
$checkbox-color: #999;
// =========form font=========
$form-font-size: 14px !default;
$form-line-height: 24px !default;
$form-input-height: 50px !default;
$form-letter-spacing: 0.1em !default;
// =========form layout=========
$form-border-width: 2px !default;
$form-input-space-vt: floor(($form-input-height - $form-line-height - $form-border-width*2)/2) !default; // верхний паддинг округляем в нижнюю сторону
$form-input-space-vb: ceil(($form-input-height - $form-line-height - $form-border-width*2)/2) !default; // нижний паддинг округляем в верхнюю сторону
$form-input-space-h: 18px !default;
// =========select=========
$select-space-v: 12px !default;
$select-space-h: $form-input-space-h !default;
$select-arrow-width: 40px !default;
$select-arrow-height: 100% !default;
$multiple-item-space: 3px !default;

// slider
$slider-btn-size: 60px 100% !default; // width, height
$slider-btn-size--sm: 40px 100% !default; // width, height
$slider-arr-size: 30px !default; // width, height
$slider-arr-size--sm: 13px !default; // width, height

// search form
$search-form--size: 164px auto !default; // width, height
$search-form--btn-size: 50px auto !default; // width, height
$search-form--btn-icon-size: 18px 18px !default;

// main-banners
$main-banners-ratio: 45.25% 54.75% !default;

// new
$new-ratio: 37% 63% 54% 46% !default;
$new-space: 10px !default;

// products previews
$products-outer-space: 0 20px !default; // vertical horizontal
$products-inner-space: 20px 20px !default; // vertical horizontal
$products-rang: 12px !default;
$products-shade-size: 18px !default;

// events slider
$events-size: 373 278 !default; // horizontal vertical, без единиц измерения
$events-inner-space: 6px !default; // vertical horizontal
$events-rang: 0.1 !default; // относитлеьно размера элемента
$events-rang-size: round(nth($events-size, 2)*$events-rang*1px) round(nth($events-size, 1)*$events-rang*1px) !default; // vertical horizontal
$events-shade-size: 18px !default;

// social color
$facebook-color: #3a589e !default;
$vkontakte-color: #4b73a4 !default;
$twitter-color: #1da0f2 !default;
$odnoklassniki-color: #f2822e !default;
$youtube-color: #e62117 !default;
$instagram-color-start: #de4f5a !default;
$instagram-color-end: #a43b97 !default;

// =========Buttons=========
$btn-bg-color: $base-color !default;
//$btn-bg-hover-color: darken($btn-bg-color, 20%) !default
$btn-bg-hover-color: $text-color !default;
$btn-text-color: white !default;
$btn-disabled-color: #ccc !default;

// =========Animation=========
$animation-speed: 0.2s !default;
$animation-speed-mouseleave: 0.35s !default;

// =========fonts style=========
// основоной шрифт
$font-norm: 'proximanova-regular', sans-serif !default;
$font-bold: 'proximanova-bold', sans-serif !default;
$font-bold-italic: 'proximanova-bold-italic', sans-serif !default;
$font-italic: 'proximanova-italic', sans-serif !default;
$font-light: 'proximanova-light', sans-serif !default;
$font-light-italic: 'proximanova-light-italic', sans-serif !default;
$font-semibold: 'proximanova-semibold', sans-serif !default;
// дополнительный шрифт
$font-alt: 'futura-bold', sans-serif !default;
$font-alt-bold: 'futura-bold', sans-serif !default;
$font-alt-book: 'futura-book', sans-serif !default;
$font-alt-medium: 'futura-medium', sans-serif !default;

// ============= обнуление жирности и наклонности шрифта ==============
// ============= использовать, если подключается font-face ==============
@mixin font-reset() {
	font: {
		weight: normal;
		style: normal; } }

// ============= основоной шрифт ==============
@mixin font-regular() {
	font: {
		family: $font-norm; }
	@include font-reset; }

@mixin font-bold() {
	font: {
		family: $font-bold; }
	@include font-reset; }

@mixin font-bold-italic() {
	font: {
		family: $font-bold-italic; }
	@include font-reset; }

@mixin font-italic() {
	font: {
		family: $font-italic; }
	@include font-reset; }

@mixin font-light() {
	font: {
		family: $font-light; }
	@include font-reset; }

@mixin font-light-italic() {
	font: {
		family: $font-light-italic; }
	@include font-reset; }

@mixin font-semibold() {
	font: {
		family: $font-semibold; }
	@include font-reset; }

// ========== дополнительный шрифт ============
@mixin font-alt() {
	font: {
		family: $font-alt-bold; }
	@include font-reset; }

@mixin font-alt-bold() {
	font: {
		family: $font-alt-bold; }
	@include font-reset; }

@mixin font-alt-book() {
	font: {
		family: $font-alt-book; }
	@include font-reset; }

@mixin font-alt-medium() {
	font: {
		family: $font-alt-medium; }
	@include font-reset; }
