// =============================================================================
// Font Face
// =============================================================================
/* proximanova */
@include font-face("proximanova-bold", "../fonts/proximanova/bold/proximanova-bold", normal, normal, eot woff ttf svg);
@include font-face("proximanova-bold-italic", "../fonts/proximanova/bold-italic/proximanova-bold-italic", normal, normal, eot woff ttf svg);
@include font-face("proximanova-italic", "../fonts/proximanova/italic/proximanova-italic", normal, normal, eot woff ttf svg);
@include font-face("proximanova-light", "../fonts/proximanova/light/proximanova-light", normal, normal, eot woff ttf svg);
@include font-face("proximanova-light-italic", "../fonts/proximanova/light-italic/proximanova-light-italic", normal, normal, eot woff ttf svg);
@include font-face("proximanova-regular", "../fonts/proximanova/regular/proximanova-regular", normal, normal, eot woff ttf svg);
@include font-face("proximanova-semibold", "../fonts/proximanova/semibold/proximanova-semibold", normal, normal, eot woff ttf svg);
/* futura-pt */
@include font-face("futura-bold", "../fonts/futura-pt/bold/futura-bold", normal, normal, ttf);
@include font-face("futura-book", "../fonts/futura-pt/book/futura-book", normal, normal, ttf);
@include font-face("futura-medium", "../fonts/futura-pt/medium/futura-medium", normal, normal, ttf);
