	display: inline-block;
	vertical-align: middle;
	max-width: calc(100% - 5px);
	font-size: 1.6rem;
	line-height: 1.25;
	letter-spacing: normal;
	padding: 8px 0;
}

.no-touchevents .nav__list a:hover {
	color: #f70106;
}

.nav__list li.has-drop > .nav__tab > a {
	padding-right: 60px;
}

.nav__list li.current > .nav__tab a, .nav__list li.current > .nav__tab a:hover, .no-touchevents .nav__list li.current > .nav__tab a, .no-touchevents .nav__list li.current > .nav__tab a:hover {
	color: #f70106;
}

.nav__list li.is-open > .nav__tab a::before {
	opacity: 1;
	transform: translate(0, 0);
}

.nav__list > li > .nav__tab a {
	min-height: 0;
	font-family: "futura-bold", sans-serif;
	font-weight: normal;
	font-style: normal;
	text-transform: uppercase;
	color: #000;
}

.nav__list > li > .nav__tab a span {
	letter-spacing: 0.1em;
	font-size: 1.6rem;
	line-height: 1.25;
}

.nav__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: 50px;
	height: 100%;
	transition-property: transform, color, background-color;
	transition-duration: 0.2s;
	z-index: 2;
	cursor: pointer;
	display: none;
}

.touchevents .nav__angle {
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

li.has-drop > .nav__tab > .nav__angle {
	display: block;
}

.nav__angle i {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 16px;
	height: 16px;
	margin: -8px 0 0 -8px;
	pointer-events: none;
	transition-property: transform;
	transition-duration: 0.2s;
	font-size: 0;
	line-height: 0;
}

.nav__angle i::before, .nav__angle i::after {
	content: "";
	display: block;
	position: absolute;
	top: 50%;
	left: 0;
	height: 2px;
	width: 16px;
	backface-visibility: hidden;
	background-color: #000;
	transition-property: background-color, transform;
	transition-duration: 0.2s;
}

.nav__angle i::after {
	transform: rotate(0.25turn);
}

li.is-open > .nav__tab .nav__angle i::before, li.is-open > .nav__tab .nav__angle i::after {
	transform: rotate(0);
}

.no-touchevents li > .nav__tab .nav__angle:hover i::before, .no-touchevents li > .nav__tab .nav__angle:hover i::after {
	background-color: #f70106;
}

.nav__drop a {
	padding-left: 45px;
	color: #000;
	text-transform: uppercase;
}

.nav__drop a span {
	letter-spacing: 0.05em;
	font-size: 1.6rem;
	line-height: 1.25;
}

.nav__drop .nav__drop a {
	color: #999;
	text-transform: none;
}

.nav__drop .nav__drop a span {
	font-size: 1.6rem;
	line-height: 1.25;
}

.nav__drop .nav__drop .nav__drop a {
	padding-left: 60px;
	color: #999;
	text-transform: none;
}

.nav__drop .nav__drop .nav__drop a span {
	font-size: 1.4rem;
	line-height: 1.29;
}

/* nav list */
/* === NAVIGATION end === */
/* === SHUTTERS === */
/* shutter layout */
.shutter {
	overflow: hidden;
	position: fixed;
	height: calc(100% - 70px);
	left: 0;
	top: 70px;
	z-index: 10000;
	will-change: transform;
	transform: translate(-100%, 0);
	transform: translateX(-100%);
	background-color: white;
	transition-property: transform;
	transition-duration: 0.2s;
}

@media (max-width: 1365px) {
	.shutter {
		height: calc(100% - 50px);
		top: 50px;
	}
}

.shutter.active {
	transform: translate(0, 0);
	transform: translateX(0);
}

.shutter__content {
	width: calc(100% + 100px);
	overflow-x: hidden;
	overflow-y: auto;
	height: 100%;
}

.touchevents .shutter__content {
	width: 100%;
}

.shutter, .shutter__content__holder {
	width: 280px;
}

@media (max-width: 639px) {
	.shutter, .shutter__content__holder {
		width: 100%;
	}
}

/* shutter layout end */
/* shutter mask */
.shutter__mask {
	position: absolute;
	left: -15px;
	width: calc(100% + 30px);
	height: 30px;
	pointer-events: none;
	z-index: 25;
}

.shutter__mask.shutter__mask--start {
	top: 0;
	background: white;
	background: -webkit-linear-gradient(top, white 0%, white 25%, rgba(255, 255, 255, 0) 100%);
	background: linear-gradient(to bottom, white 0%, white 25%, rgba(255, 255, 255, 0) 100%);
}

.shutter__mask.shutter__mask--end {
	bottom: 0;
	background: white;
	background: -webkit-linear-gradient(bottom, white 0%, white 25%, rgba(255, 255, 255, 0) 100%);
	background: linear-gradient(to top, white 0%, white 25%, rgba(255, 255, 255, 0) 100%);
}

/* shutter mask end */
/* shutter options */
.shutter__options {
	display: none;
	position: relative;
	height: 50px;
	box-shadow: inset 0 1px 0 #666, 0 0 30px 10px white;
	z-index: 10;
}

/* shutter options end */
/* button shutter close */
.btn-shutter-close {
	display: block;
	width: 50px;
	height: 50px;
	background: #f70106;
	color: white;
	text-decoration: none;
	font-size: 0;
	letter-spacing: -4px;
	white-space: nowrap;
	transition-property: background-color, opacity, visibility;
	transition-duration: 0.2s;
}

.btn-shutter-close::before {
	content: '';
	display: inline-block;
	height: 50px;
	min-height: 100%;
	vertical-align: middle;
}

.shutter__options .btn-shutter-close {
	position: absolute;
	left: 0;
	top: 0;
	border: solid #666;
	border-width: 0 1px 0 0;
}

.btn-shutter-close > span {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.btn-shutter-close > span:active, .btn-shutter-close > span:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}

.btn-shutter-close > i {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	background-color: transparent;
	width: 22px;
	height: 2px;
	border-radius: 0;
}

.btn-shutter-close > i::before, .btn-shutter-close > i::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	width: 22px;
	height: 2px;
	border-radius: 0;
	background: white;
	transition-property: background-color;
	transition-duration: 0.2s;
}

.btn-shutter-close > i::before {
	top: 0;
	transform: rotate(-45deg);
}

.btn-shutter-close > i::after {
	top: 0;
	transform: rotate(45deg);
}

/* button shutter close end */
/* shutter--nav */
@media (max-width: 1199px) {
	.shutter--nav {
		border-top: 1px solid #666;
		padding-top: 50px;
	}
}

@media (max-width: 639px) {
	.shutter--nav {
		width: 100%;
	}
}

.shutter--nav .shutter__mask--start {
	top: 50px;
}

@media (max-width: 1199px) {
	.shutter--nav .shutter__mask--start {
		background: #1a1a1a;
		background: -webkit-linear-gradient(top, #1a1a1a 0%, #1a1a1a 25%, rgba(26, 26, 26, 0) 100%);
		background: linear-gradient(to bottom, #1a1a1a 0%, #1a1a1a 25%, rgba(26, 26, 26, 0) 100%);
	}
}

@media (max-width: 767px) {
	.shutter--nav .shutter__options {
		display: block;
	}
}

/* shutter--nav end */
/* === SHUTTERS end === */
