@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	&.css-scroll-fixed {
		@include scroll-blocked(); } } // блокировка скролла

/* === LAYOUT === */
.max-wrap {
	max-width: $page-layout-width + nth($page-layout-space, 2)*2; }

.layout {
	@if nth($page-layout-space, 2) != 0 {
		@include padding-horizontal(nth($page-layout-space, 2)); }

	@include push-auto; }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
	padding: round($text-layout-space) #{$text-layout-space} round($text-layout-space*1.5);

	@include media('<tab-lg') {
		padding: round($text-layout-space--sm*1.5) #{$text-layout-space--sm} round($text-layout-space--sm*2); }

	@include media('<tab-sm') {
		padding: round($text-layout-space--mob*1.5) #{$text-layout-space--mob} round($text-layout-space--mob*2); }

	& > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text, .imgc figcaption {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	.layout-full-width, .layout-wide {

		@include margin-horizontal(-#{$text-layout-space});

		@include media('<tab-lg') {
			@include margin-horizontal(-#{$text-layout-space--sm}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{$text-layout-space--mob}); } } }

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	max-width: #{$text-layout-width + $text-layout-space*2};

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + $text-layout-space--sm*2}; }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + $text-layout-space--mob*2}; } }

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	max-width: #{$text-layout-width + $text-layout-space*2};
	@include padding-horizontal($text-layout-space);
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + $text-layout-space--sm*2};
		@include padding-horizontal($text-layout-space--sm); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + $text-layout-space--mob*2};
		@include padding-horizontal($text-layout-space--mob); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	max-width: #{$flood-layout-width + $text-layout-space*2};

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + $text-layout-space--sm*2}; }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + $text-layout-space--mob*2}; } }

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + $text-layout-space*2};
	@include padding-horizontal($text-layout-space);
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + $text-layout-space--sm*2};
		@include padding-horizontal($text-layout-space--sm); }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + $text-layout-space--mob*2};
		@include padding-horizontal($text-layout-space--mob); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
	@include padding-horizontal($text-layout-space);

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space--sm); }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space--mob); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// лэяут страницы
.wrapper {
	display: block;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	overflow: hidden;
	position: relative;
	margin: 0 auto;
	min-height: 100vh; }

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	///* Костыль */
	///* IE10 IE11 не тянет .wrapper с display: flax */
	/* IE10 IE11 */
	.wrapper {
		display: block; } }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex: 1;
	position: relative;
	clear: both;
	z-index: 4;
	padding-top: $header-height;

	@include media('<desk-md') {
		padding-top: $header-height--sm; }

	.no-js & {
		padding-top: 0; } }

.main__holder {
	width: 100%;
	@include clear; }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	margin: 0 200px 0 300px;
	min-height: 50px;

	background-color: whitesmoke;

	@include media('<desk-lg') {
		margin-right: 0; }

	@include media('<tab-lg') {
		margin-left: 0; } }


/* === CONTENT end === */

/* === HEADER === */
/* HEADER */
// header layout
.header {
	position: fixed;
	background-color: #1a1a1a;
	color: white;
	width: 100%;
	min-width: 320px;
	height: $header-height;
	z-index: 10;
	// ===================
	opacity: 0;
	visibility: hidden;
	transform: translate(0, -80%);
	transform: translate3d(0, -80%, 0);
	$props: opacity, transform, visibility;
	will-change: $props;
	transition: {
		property: $props;
		duration: $animation-speed; }

	.header-show &, .shutter-after-open & {
		// ===================
		opacity: 1;
		visibility: visible;
		transform: translate(0, 0);
		transform: translate3d(0, 0, 0); }

	@include media('<desk-md') {
		height: $header-height--sm; }

	.no-js & {
		position: relative; }

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: -20px;
		top: 0;
		height: 100%;
		width: calc(100% + 40px);
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
		pointer-events: none;
		opacity: 0;
		will-change: opacity;
		transition: {
			property: opacity;
			duration: $animation-speed; }

		.page-is-scrolled & {
			opacity: 1; } }

	.btn-menu {
		@include size($header-height);
		position: absolute;
		left: 0;
		top: 0;
		z-index: 5;

		@include media('<desk-md') {
			@include size($header-height--sm); } } }

.header > .layout {
	height: 100%; }

.header__holder {
	height: 100%;
	text-align: center;
	@include clear-spacing;
	@include clear; }

.header__col {
	@include display-inline-block-top;
	position: relative;
	text-align: left;
	height: 100%;
	white-space: nowrap;
	@include clear-spacing;

	&:first-child {
		display: block;
		float: left;
		padding-left: $header-height;

		@include media('<desk-md') {
			padding-left: $header-height--sm; } }

	&:last-child {
		display: block;
		float: right; }

	&::before {
		@include pseudo-content();
		@include display-inline-block-middle;
		height: 100%; } }

.header__cell {
	@include display-inline-block-middle;
	@include font($font-size, $line-height);
	white-space: normal;
	letter-spacing: normal; }

// header layout end

/* menu */
.header__col--menu {
	.header__cell, .menu-list, .menu-list > li, .menu-list > li > a {
		height: 100%; } }

.menu-list {
	$ml-space: 30px;
	$ml-space--md: 20px;
	$ml-space--sm: 10px;
	// ==================
	list-style: none;
	@include clear-spacing;

	@include media('<desk-lg') {
		padding-left: 0; }

	@include media('<desk-sm') {
		display: none; }

	li {
		@include display-inline-block-middle;
		@include font-bold;
		@include font(1.4rem, round-float(18/14, 2));
		letter-spacing: 0.1em;
		text-transform: uppercase;

		@include media('<desk-md') {
			font-size: 1.3rem;
			letter-spacing: 0.05em; } }

	a {
		display: table;
		width: 100%;
		text-decoration: none;
		color: white;

		// убрать подсветку при клике на тачскринах
		.touchevents & {
			user-select: none;
			-webkit-tap-highlight-color: transparent; }

		span {
			display: table-cell;
			vertical-align: middle;
			position: relative;
			@include padding-horizontal(round($ml-space/2));

			@include media('<desk-lg') {
				@include padding-horizontal(round($ml-space--md/2)); }

			@include media('<desk-md') {
				@include padding-horizontal(round($ml-space--sm/2)); }

			&::after {
				content: "";
				display: block;
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				border-top: 3px solid white;
				will-change: opacity;
				opacity: 0;
				transition: {
					property: opacity;
					duration: $animation-speed; } } } }

	// text-mark
	.text-mark {
		a {
			color: $base-color;

			span::after {
				border-color: $base-color; } } }

	// hover
	a {
		&:hover {
			span::after {
				opacity: 1; } } }

	// in shutter, for mobile
	.shutter & {
		display: none;
		border-bottom: 1px solid $border-color;
		padding: nth($shutter-space, 1) 0;
		background-color: $header-bg;

		@include media('<desk-sm') {
			display: block; }

		li {
			display: block;
			@include font(1.4rem, round-float(20/16, 2)); }

		a {
			padding: 0 nth($shutter-space, 2);
			color: inherit;
			color: white;

			span {
				padding: 8px 0;

				&::after {
					content: none; } } }

		// text-mark
		.text-mark {
			a {
				color: $base-color;

				span::after {
					border-color: $base-color; } } }

		// hover
		a {
			&:hover {
				color: $base-color; } } } }

/* menu end */

/* logo */
.logo {
	display: block;
	position: relative;
	width: nth($logo-size, 1);

	@include media('<desk-lg') {
		width: 200px; }

	@include media('<desk-md') {
		width: 160px; }

	@include media('<mob-lg') {
		width: 120px; }

	@include media('<mob-sm') {
		width: 100px; }

	&::before {
		content: "";
		display: block;
		@include ratio(nth($logo-size, 1), nth($logo-size, 2));
		background-repeat: no-repeat;
		background-position: left;
		background-color: $header-bg;
		background-image: url(../../img/logo.png);
		background-size: contain;

		@include media('<mob-lg') {
			background-image: url(../../img/logo-mob.png); } }

	a, & > span {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		@include size(100%);
		z-index: 5;

		& > em {
			@include hide-text; } } }

/* logo end */

.header__col--options {
	.header__cell {
		padding-left: nth($search-form--size, 1);
		position: relative;

		@include media('<tab-lg') {
			padding-left: 0; } } }

/* search-form */
.search-form {
	position: relative;
	margin: 0;
	width: nth($search-form--size, 1);
	border: solid #666;
	border-width: 0 1px;

	.header & {
		position: absolute;
		top: 50%;
		right: 100%;
		margin: -#{round($header-height/2)} -#{nth($search-form--size, 1)} 0 #{nth($search-form--size, 1)};

		@include media('<desk-md') {
			margin: -#{round($header-height--sm/2)} -#{nth($search-form--size, 1)} 0 #{nth($search-form--size, 1)}; }

		@include media('<tab-lg') {
			display: none; } }

	.shutter--nav & {
		display: none;
		position: absolute;
		left: 0;
		top: -1px; // если есть бордер на мобильном меню сверху
		border-width: 1px 0;
		width: 100%;
		z-index: 100;

		@include media('<tab-lg') {
			display: block; } }

	.search-form__field {
		margin: 0; }

	.search-form__input {
		padding: 0 10px 0 nth($search-form--btn-size, 1);
		height: $header-height;
		width: 100%;
		border: none;
		background: $header-bg;
		@include font-bold;
		@include font(1.6rem, $header-height);
		letter-spacing: normal;

		@include placeholder {
			color: white; }

		@include placeholder-focus {
			color: $text-color;
			opacity: 1; }

		@include media('<desk-md') {
			height: $header-height--sm;
			line-height: $header-height--sm; }

		&, &:hover, &:focus, &.error, &.input--has-value {
			box-shadow: none; }

		&:focus, &.input--has-value {
			background-color: white;
			color: $text-color; } } }

.search-form__btn {
	position: absolute;
	left: 0;
	top: 50%;
	@include size(nth($search-form--btn-size, 1), $header-height);
	margin-top: -#{round($header-height*0.5)};
	@include font-regular;
	text-align: center;
	z-index: 5;
	color: white;
	pointer-events: none;

	@include media('<desk-md') {
		height: $header-height--sm;
		margin-top: -#{round($header-height--sm*0.5)}; }

	.input--has-value &, .search-form__input:focus ~ & {
		color: $text-color; }

	.input--has-value & {
		pointer-events: auto; }

	.no-touchevents &:hover {
		color: $base-color; }

	input {
		@include cover-abs();
		padding: 0;
		border: 0;
		opacity: 0;
		z-index: 99; }

	svg {
		display: inline-block;
		position: absolute;
		@include position(50%);
		@include size(nth($search-form--btn-icon-size, 1));
		margin: -#{round(nth($search-form--btn-icon-size, 1)*0.5)} 0 0 -#{round(nth($search-form--btn-icon-size, 1)*0.5)}; }

	path {
		fill: currentColor;
		transition: inherit; }

	span {
		@include hide-text; } }

/* search-form end */
/* HEADER end */
/* === HEADER end === */

/* === SIDEBAR === */
.sidebar {
	display: inline;
	float: left;
	margin: 0 0 0 -100%;
	width: $sidebar-width;

	background-color: sandybrown;

	@include media('<tab-lg') {
		display: none; } }

/* === SIDEBAR end === */

/* === ASIDE === */
.aside {
	display: inline;
	float: right;
	margin: 0 0 0 -#{$aside-width};
	width: $aside-width;
	background-color: sandybrown;

	@include media('<desk-lg') {
		display: none; } }

/* === ASIDE end === */

/* === FOOTER === */
.footer {
	//margin: 0 auto
	position: relative;
	clear: both;
	z-index: 5;
	@include font(16px, round-float(26/16, 3)); }

.footer__holder {
	overflow: hidden;
	cursor: default; }

/* footer common */
.footer-common {
	position: relative;
	background: #eceff1;
	padding: round($text-layout-space*2) $text-layout-space round($text-layout-space*1.5);

	@include media('<tab-lg') {
		padding: round($text-layout-space--sm*2) $text-layout-space--sm; }

	@include media('<mob-lg') {
		padding: round($text-layout-space--mob*2) $text-layout-space--mob; } }

.footer-common__group {
	margin: 0 -#{round($text-layout-space/2)} round($text-layout-space*1.5);
	@include clear;

	@include media('<tab-lg') {
		margin: 0 -#{round($text-layout-space--sm/2)} round($text-layout-space--sm*2); }

	@include media('<mob-lg') {
		margin: 0 -#{round($text-layout-space--mob/2)} round($text-layout-space--mob*2); }

	&:last-child {
		margin-bottom: 0; } }

.footer-common__box {
	@include padding-horizontal(round($text-layout-space/2));
	width: 50%;
	max-width: 680px;

	@include media('<tab-lg') {
		max-width: none;
		margin-top: $text-layout-space--sm; }

	&:first-child {
		float: left;

		@include media('<desk-sm') {
			width: 65%; }

		@include media('<tab-lg') {
			float: none;
			width: auto;
			margin-top: 0; } }

	&:last-child {
		float: right;

		@include media('<desk-sm') {
			width: 35%; }

		@include media('<tab-lg') {
			float: none;
			width: auto; } } }

.footer-nav {
	@include grid-parent($text-layout-space);
	@include clear-spacing;

	@include media('<tab-lg') {
		@include grid-parent($text-layout-space--sm); } }

.footer-nav__box {
	@include grid-item(4, $text-layout-space);
	@include font(1.6rem, 26px);
	letter-spacing: normal;

	@include media('<tab-lg') {
		@include grid-item-responsive(4, $text-layout-space--sm); }

	@include media('<mob-lg') {
		@include grid-item-responsive(2, $text-layout-space--sm); }

	& > *:last-child {
		margin-bottom: 0; }

	.footer-common__box--navigator & {
		width: 100%;

		@include media('<tab-lg') {
			text-align: center; } } }

.footer-nav__title, .footer-nav__list {
	position: relative;
	display: block;
	@include font-alt-bold;
	font-size: 1.8rem;
	margin-bottom: 0.8em;

	@include media('<mob-lg') {
		margin-bottom: 0.5em; } }

.footer-nav__title {
	a {
		color: black;
		text-decoration: none;

		&:hover {
			color: $base-color; } } }

.footer-nav__links {
	list-style: none;
	margin: 0 0 0.8em; }

.footer-nav__list {
	list-style: none;
	@include font-alt-bold;
	font-size: 18px;
	color: black; }

.footer-nav__links, .footer-nav__list {

	li {
		margin-bottom: 0.8em;

		@include media('<mob-lg') {
			margin-bottom: 0.5em; }

		&:last-child {
			margin-bottom: 0; } }

	a {
		padding-top: 0.5em;
		color: #666;
		text-decoration: none;

		&:hover {
			color: $base-color; } } }

.footer-nav__list {
	a {
		color: inherit; } }

/* footer common end */

/* navigator */
.navigator {
	position: relative;

	@include media('<tab-lg') {
		margin: 0 auto;
		max-width: 600px; } }

.btn-navigator {
	$space: 20px;
	//==============
	position: absolute;
	left: $space;
	top: $space;
	z-index: 25;
	padding: 13px;
	width: 280px;
	max-width: calc(100% - #{round($space*2)});
	@include font-bold;
	line-height: 1;
	color: black;
	background: #f3f3f3;
	border: 2px solid #ccc;

	& > span {
		@include font(1.4rem, round-float(20/14, 2));
		text-transform: uppercase;
		letter-spacing: 0.1em; }

	// hover
	.no-touchevents &:hover {
		color: $base-color;
		border-color: $base-color; } }

/* navigator end */

/* footer bottom */
.footer-bottom {
	padding: 50px $text-layout-space 40px;

	@include media('<tab-lg') {
		padding: round($text-layout-space--sm*2) $text-layout-space--sm; }

	@include media('<mob-lg') {
		padding: round($text-layout-space--mob*2) $text-layout-space--mob; } }

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;

	@include clear-spacing;

	&::after {
		content: "1";
		display: inline-block;
		margin-left: 100%;
		height: 1px;
		overflow: hidden;

		@include media('<tab-lg') {
			content: none; } } }

.footer-bottom__col {
	@include display-inline-block-top;
	@include padding-horizontal(round($text-layout-space/2));
	@include font(16px, round-float(24/16, 3));
	letter-spacing: normal;
	width: 33%;
	text-align: left;

	@include media('<tab-lg') {
		display: block;
		width: auto;
		max-width: none;
		padding: 0;
		text-align: center;
		margin-top: $text-layout-space--sm; }

	&:first-child {
		padding-left: 0;

		@include media('<tab-lg') {
			margin-top: 0; } }

	&:last-child {
		max-width: 380px;
		padding-right: 0;

		@include media('<tab-lg') {
			max-width: none; } }

	a {
		color: $text-color;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } } }

.address {
	margin-bottom: $text-layout-space;

	@include media('<tab-lg') {
		margin-bottom: $text-layout-space--sm; }

	&:last-child {
		margin-bottom: 0; }

	.dl {
		.dt {
			display: inline; }

		.dd {
			display: inline; } }

	a {
		text-decoration: underline;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.footer-logo {
	display: block;
	margin: 0 auto;

	@include media('<tab-lg') {
		max-width: 320px; } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px); }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	//.no-touchevents a:hover
	//ransform: translate(0, 1px)
	//ilter: blur(2px)
	//pacity: 0.6

	&::after {
		@include pseudo();
		filter: invert(1);
		background: #666 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			filter: invert(0);
			background-color: #00ef85;
 } } }			//transform: translate(0, -1px)

/* developer end */
/* === FOOTER end === */

/* === NAVIGATION === */
/* nav list */
// общие стили для главного списка навигации
.nav__list {
	position: relative;

	@include media('<tab-lg') {
		width: auto; } }

// общие стили для подменю навигации
.nav__drop {
	position: relative;
	//background-color: $nav-bg-color
	display: none;

	.no-js & {
		display: block; } }

// общие стили для пунктов навигации
.nav__list {
	padding: nth($shutter-space, 1) 0;

	li {
		position: relative;
		overflow: hidden; }

	.nav__tab {
		position: relative; }

	a {
		display: block;
		position: relative;
		padding: 0 nth($shutter-space, 2);
		@include font-light;
		color: #999;
		text-decoration: none;
		transition-property: color, background-color;
		transition-duration: $animation-speed;
		@include clear-spacing;

		// убрать подсветку при клике на тачскринах
		.touchevents & {
			user-select: none;
			-webkit-tap-highlight-color: transparent; }

		&::before {
			@include pseudo-inline-block-relative();
			vertical-align: middle;
			min-height: 36px; }

		span {
			@include display-inline-block-middle;
			max-width: calc(100% - 5px);
			@include font(1.6rem, round-float(20/16, 2));
			letter-spacing: normal;
			padding: 8px 0; }

		.no-touchevents &:hover {
			color: $base-color; } } }

// стили для пунктов навигации, у которых ЕСТЬ ПОДМЕНЮ
.nav__list li.has-drop > .nav__tab > a {
	padding-right: (round($nav-arrow-size+10)); }

// стили для пункта навигации ТЕКУЩЕЙ страницы
.nav__list li.current > .nav__tab {

	&, .no-touchevents & {

		a, a:hover {
			color: $base-color; } } }

// стили для пункта навигации С ОТКРЫТЫМ ПОДМЕНЮ
.nav__list li {
	&.is-open {
		& > .nav__tab a {
			&::before {
				opacity: 1;
				transform: translate(0, 0); } } } }

//& > .nav__drop
//isplay: block

// нужно добавлять на nav__drop style="display: block"

// особые стили для пунктов навигации ПЕРВОГО урованя
.nav__list > li {

	& > .nav__tab a {
		min-height: 0;
		@include font-alt-bold;
		text-transform: uppercase;
		color: $text-color;

		span {
			letter-spacing: 0.1em;
			@include font(1.6rem, round-float(20/16, 2)); } } }

// СТРЕЛКИ навигации
.nav__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: $nav-arrow-size;
	height: 100%;
	//border: solid #666
	//border-width: 0 0 0 1px
	transition-property: transform, color, background-color;
	transition-duration: $animation-speed;
	z-index: 2;
	cursor: pointer;
	display: none;

	// убрать подсветку при клике на тачскринах
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li.has-drop > .nav__tab > & {
		display: block; }

	i {
		position: absolute;
		@include position(50%, 50%);
		@include size(16px);
		margin: -8px 0 0 -8px;
		pointer-events: none;
		transition-property: transform;
		transition-duration: $animation-speed;
		@include font(0, 0);

		&::before, &::after {
			@include pseudo();
			@include position(50%, 0);
			height: 2px;
			width: 16px;
			backface-visibility: hidden;
			background-color: $nav-text-color;
			transition-property: background-color, transform;
			transition-duration: $animation-speed; }

		&::after {
			transform: rotate(0.25turn); } } }

// стили стрелок навигации для разных состояний
.nav__angle {

	// активное (подменю открыто)
	li.is-open > .nav__tab & i {
		//transform: rotate(0)

		&::before, &::after {
			transform: rotate(0); } }

	// ховер
	.no-touchevents li > .nav__tab &:hover i {
		&::before, &::after {
			background-color: $base-color; } } }

// особые стили для пунктов ПОДМЕНЮ
.nav__drop {

	// особые стили для пунктов навигации ВТОРОГО урованя
	//ul, a, .nav__angle
	//ackground-color: $nav-bg-color-s

	a {
		padding-left: round(nth($shutter-space, 2) + nth($shutter-space, 2)/2);
		color: $text-color;
		text-transform: uppercase;

		span {
			letter-spacing: 0.05em;
			@include font(1.6rem, round-float(20/16, 2)); } }

	.nav__drop {
		// особые стили для пунктов навигации ТРЕТЬЕГО урованя
		//ul, a, .nav__angle
		//ackground-color: $nav-bg-color-ss

		a {
			color: #999;
			text-transform: none;

			span {
				@include font(1.6rem, round-float(20/16, 2)); } }

		.nav__drop {
			// особые стили для пунктов навигации ТРЕТЬЕГО урованя
			//ul, a, .nav__angle
			//ackground-color: $nav-bg-color-sss

			a {
				padding-left: round(nth($shutter-space, 2)*2);
				color: #999;
				text-transform: none;

				span {
					@include font(1.4rem, round-float(18/14, 2)); } } } } }

/* nav list */

/* === NAVIGATION end === */

/* === SHUTTERS === */
/* shutter layout */
.shutter {
	overflow: hidden; // необходим для скрытия скролла
	position: fixed;
	height: calc(100% - #{$header-height});
	left: 0;
	top: $header-height;
	z-index: 10000;
	// ==========================
	//opacity: 0
	//visibility: hidden
	will-change: transform;
	transform: translate(-100%, 0);
	transform: translateX(-100%);
	background-color: white;
	transition: {
		property: transform;
		duration: $animation-speed; }

	@include media('<desk-md') {
		height: calc(100% - #{$header-height--sm});
		top: $header-height--sm; }

	&.active {
		transform: translate(0, 0);
		transform: translateX(0); } }

.shutter__content {
	width: calc(100% + 100px); // скрываем скролл на десктопе
	overflow-x: hidden;
	overflow-y: auto;
	height: 100%;

	.touchevents & {
		width: 100%; } } // возвращаем скролл на тачскрине

.shutter, .shutter__content__holder {
	// ширину устанавливаем на основной
	// и вложенный контейнер,
	// чтобы корректно обрезать скролл
	width: $shutter-width;

	@include media('<mob-lg') {
		width: 100%; } }

/* shutter layout end */

/* shutter mask */
.shutter__mask {
	$shutter-mask-size: 40px;
	$shutter-mask-color: white;
	//======================
	position: absolute;
	//left: -#{round($shutter-mask-size/2)}
	//width: calc(100% + #{$shutter-mask-size})
	//height: $shutter-mask-size
	//box-shadow: 0 0 nth($nav-space, 2) white
	left: -#{round(nth($nav-space, 2)/2)};
	width: calc(100% + #{nth($nav-space, 2)});
	height: nth($nav-space, 2);
	pointer-events: none;
	z-index: 25;

	&.shutter__mask--start {
		top: 0;
		@include linear-gradient(to bottom, #{$shutter-mask-color} 0%, #{$shutter-mask-color} 25%, rgba($shutter-mask-color, 0) 100%); }

	&.shutter__mask--end {
		bottom: 0;
		@include linear-gradient(to top, #{$shutter-mask-color} 0%, #{$shutter-mask-color} 25%, rgba($shutter-mask-color, 0) 100%); } }

/* shutter mask end */

/* shutter options */
.shutter__options {
	display: none;
	position: relative;
	height: $header-height--sm;
	box-shadow: inset 0 1px 0 $border-color, 0 0 30px 10px white;
	z-index: 10; }

/* shutter options end */

/* button shutter close */
.btn-shutter-close {
	//<a href="#" class="btn-shutter-close js-btn-shutter-close"><i>&nbsp;</i><span>Закрыть</span></a>
	$btn-shutter-close--bg: $btn-bg-color;
	$btn-shutter-close--color: $text-color;
	$btn-shutter-close--size: $header-height--sm;
	$btn-shutter-close--space: 12px;
	$btn-shutter-close--lines-width: 22px;
	$btn-shutter-close--lines-height: 2px;
	$btn-shutter-close--lines-space: 5px;
	$btn-shutter-close--lines-radius: 0;
	$btn-shutter-close--lines-color: white;

	display: block;
	@include size($btn-shutter-close--size);
	background: $btn-shutter-close--bg;
	color: white;
	text-decoration: none;
	@include clear-spacing;
	white-space: nowrap;
	transition-property: background-color, opacity, visibility;
	transition-duration: $animation-speed;

	&::before {
		content: '';
		display: inline-block;
		height: $btn-shutter-close--size;
		min-height: 100%;
		vertical-align: middle; }

	.shutter__options & {
		position: absolute;
		left: 0;
		top: 0;
		border: solid $border-color;
		border-width: 0 1px 0 0; }

	> span {
		@include hide-text; }

	> i {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		//backface-visibility: hidden
		background-color: transparent;
		@include size($btn-shutter-close--lines-width, $btn-shutter-close--lines-height);
		border-radius: $btn-shutter-close--lines-radius;

		&::before, &::after {
			@include pseudo();
			left: 0;
			//backface-visibility: hidden
			@include size($btn-shutter-close--lines-width, $btn-shutter-close--lines-height);
			border-radius: $btn-shutter-close--lines-radius;
			background: $btn-shutter-close--lines-color;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		//first line menu
		&::before {
			top: 0;
			transform: rotate(-45deg); }

		//third line menu
		&::after {
			top: 0;
			transform: rotate(45deg); } } }

/* button shutter close end */

/* shutter--nav */
.shutter--nav {
	//position: fixed
	//left: 0
	//height: calc(100% - #{$header-height})
	//top: $header-height
	//&, .shutter__content__holder
	//idth: $nav-width

	@include media('<desk-sm') {
		border-top: 1px solid #666;
		// отступ сверху для поля поиска
		padding-top: $header-height--sm; }

	@include media('<mob-lg') {
		// для мобил на всю ширину экрана
		width: 100%; }

	.shutter__mask--start {
		// отступ сверху для поля поиска
		top: $header-height--sm;

		@include media('<desk-sm') {
			@include linear-gradient(to bottom, $header-bg 0%, $header-bg 25%, rgba($header-bg, 0) 100%); } }

	.shutter__options {
		@include media('<tab-sm') {
			display: block; } } }

/* shutter--nav end */
/* === SHUTTERS end === */
